.container{
    color: white;
    margin-top: 40px;
}

.option{
    transition: 0.3s;
    border: 3px solid #E5B8F4;
    border-radius: 15px;
    height: 75px;
    line-height: 75px;
    background-color: #2D033B;
}

.option:hover{
    color: #2D033B;
    background-color: #E5B8F4;
    border: 3px solid #2D033B
}

.timer{
    font-size: 30px;
}

h3{
    color: white;
    font-size: 30px;
}

.score{
    font-size: 35px;
}

.list{
    font-size: 22px;
}

.answers{
    list-style-type: none;
}

.answers li{
    margin-top: 15px;
}



.homepage{
    display: block;
    width: 250px;
    margin: auto;
}

@media (max-width: 768px){
   .container{
        margin-top: 20px;
    }


    .list{
        font-size: 18px;
    }

    h3{
        font-size: 22px;
    }

    .timer{
        font-size: 22px;
    }

    .option{
        height: 40px;
        line-height: 40px;
    }
}

